@keyframes spinner {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
  .loading-login-spinner {
    width: 20px;
    height: 20px;
    border: 5px solid #f3f3f3;
    /* Light grey */
    border-top: 5px solid #383636;
    /* Blue */
    border-radius: 50%;
    animation: spinner 1.5s linear infinite;
  }
  
  .login-container {
    display: grid;
    justify-content: center;
    align-items: center;
    height: 20px;
  }